.switch-button {
    margin-right: 10px; /* Adjust this value to set the desired gap between buttons */
  }
  
  .container1 {
    background-color: #6d4869;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px; /* Add margin bottom for spacing */
  }
  
  .output {
    border: 2px solid black;
    padding: 20px;
    width: 70%;
    background-color: hsl(303, 60%, 69%);
  }
  
  .welcome,
  .user-info,
  .user-type {
    padding: 10px;
    margin-bottom: 10px;
    color: rgba(90, 3, 3, 0.738); /* Set text color to white */
  }
  
  .user-type {
    font-weight: bold; /* Make user type text bold */
  }
  
  .student-details {
    margin-top: 20px;
    border: 2px solid black;
    padding: 20px;
    width: 70%;
    display: flex;
    flex-direction: row; /* Display cards side by side */
    flex-wrap: wrap; /* Allow cards to wrap to the next line if necessary */
    justify-content: center; /* Center content horizontally */
    background-color: rgb(117, 193, 218);
  }
  
  .student-card {
    width: calc(33.33% - 20px); /* Adjust the width to fit three cards in a row */
    margin: 20px 10px;
    padding: 20px;
    background-color: #b589b1;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Adding a box shadow for the 3D effect */
    transition: transform 0.3s ease; /* Adding a smooth transition for the 3D effect */
  }
  
  .student-card:hover {
    transform: translateY(-5px); /* Applying a translation on hover for the 3D effect */
  }
  
  .student-card h3 {
    margin-top: 0;
    font-size: 16px; /* Reduced font size of student name */
    color: #333; /* Set text color to a dark shade */
  }
  
  .student-card p {
    margin: 5px 0; /* Reduced paragraph margin */
    color: #555; /* Set text color to a slightly lighter shade */
  }
  
  .cont2 {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: hsl(240, 50%, 77%); /* Set background color */
    padding: 20px;
  }
  